.container {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;

  width: 100vw;
  height: 100vh;

  text-align: center;
}

.heading {
  font-size: 56px;
  font-weight: 900;
  line-height: 1;

  margin: 0;

  text-transform: capitalize;
}

.lead {
  font-size: 32px;
  font-weight: 600;

  margin: 10px 0 0;

  text-transform: lowercase;
}

.backdrop {
  display: flex;
  align-items: center;
  justify-content: center;

  height: 100%;

  background-color: var(--color-background);
}

@supports (backdrop-filter: blur(0)) {
  .backdrop {
    background-color: var(--color-background-60);

    backdrop-filter: blur(10px);
  }
}

.spinnerContainer {
  width: 200px;
  height: 200px;
}

@media (max-width: 700px) {
  .heading {
    font-size: 32px;
  }

  .lead {
    font-size: 18px;
  }
}
