.spinner {
  display: block;

  width: 100%;
  height: 100%;

  animation: rotate .5s infinite linear;

  border-top: 2px solid var(--color-spiner, var(--color-text));
  border-right: 2px solid transparent;
  border-radius: 50%;
}

@keyframes rotate {
  to {
    transform: rotate(360deg);
  }
}
